import Axios from 'axios';
import './App.css';

const App = () => {

  const handleSubmit = async(event) => {
    event.preventDefault();
    const textArea = document.getElementById('text-editor');
    const outputScreen = document.getElementById('output-screen');
    var chunk = {foramt:"py" , code: textArea.value}
    Axios.post('/run',chunk)
    .then(res => outputScreen.innerText = res.data.output)
    .catch(err => console.log(err))
  }



  return (
    <div className="app">
      <header className="app-header">
        <a href='https://yughendaran-portfolio.herokuapp.com/'><h2 className="app-title">Pygram</h2></a>
        <p className="app-sub-title">Python Online Compiler</p>
      </header>
      <div className="container-wraper">
        <form id="submit-form" onSubmit={handleSubmit}>
          <div id="workspace">
            <div className="workspace-header">
              <p className="file-name">Script.py</p>
              <button id="run">Run</button>
            </div>
            <div id="index"></div>
            <textarea spellCheck={false} id="text-editor" ></textarea>
          </div>
        </form>
        <div id="output-screen-cotainer">
            <div className="workspace-header">
              <p className="file-name">Output</p>
            </div>
            <div spellCheck={false} id="output-screen"></div>
        </div>
      </div>
    </div>
  );
}

export default App;